var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".accordion-button.collapsed[data-v-4e738051]:after,.accordion-button[data-v-4e738051]:before{background-image:url(\"data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23fff'%3E%3Cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3E%3C/svg%3E\")}", "",{"version":3,"sources":["webpack://./resources/js/components/clients/Documentation.vue"],"names":[],"mappings":"AA+LA,6FACA,2SACA","sourcesContent":["<template>\r\n    <div>\r\n        <div class=\"row\">\r\n            <div class=\"col-sm-12\"> \r\n                <NavBar />\r\n            </div>\r\n        </div>\r\n        <br />\r\n        <br />\r\n        <input type=\"hidden\" name=\"_token\" :value=\"csrf\"> \r\n        <div class=\"row\">\r\n            <div class=\"col-sm-1\">\r\n            </div>\r\n            <div class=\"col-sm-8\">\r\n                <h2 style=\"border-bottom: 1px solid #DDDDDD\">{{header_1}}</h2>\r\n                <br />\r\n                <div class=\"m-6\">\r\n                    <div class=\"accordion\" id=\"myAccordion\">\r\n                        <div class=\"accordion-item\" >\r\n                            <h2 class=\"accordion-header\" id=\"headingOne\">\r\n                                <button style=\"background-color: #5F8575\" type=\"button\" class=\"accordion-button text-light\" data-bs-toggle=\"collapse\" data-bs-target=\"#collapseOne\">1. Basic API call with Company details ONLY</button>\t\t\t\t\t\t\t\t\t\r\n                            </h2>\r\n                            <div id=\"collapseOne\" class=\"accordion-collapse collapse show\">\r\n                                <div class=\"card-body\">\r\n                                    <p>&nbsp; This API call is applicable to all Entity categories (Business Names [1], Company [2], and Incorporated Trustees [3]).</p>\r\n                                    <p>&nbsp; The API resource is invoked using the link syntax below:\r\n                                    <p class=\"text-primary\"><strong>&nbsp; http://api.cac.gov.ng/api/organizations/[username]/[password]/[apiCallType]/[entityRegistrationNumber]/[entityClassification]</strong></p>\r\n                                    <p>&nbsp; where:</p>\r\n                                    <p><b>&nbsp; [username]:</b> Your given username issued by CAC (in your case, it is <b>{{this.$session.get('username')}}</b>)</p>\r\n                                    <p><b>&nbsp; [password]:</b> Your alphanumeric password configured during your registration.</p>\r\n                                    <p><b>&nbsp; [apiCallType]:</b> API Call type signals what exactly you're requesting from CAC. For instance, <b>1</b> means requesting Entity's basic info ONLY.</p>\r\n                                    <p><b>&nbsp; [entityRegistrationNumber]:</b> Registration number of the entity you would want to search, whether BN, IT, or RC Number (e.g. RC 1662939)</p>\r\n                                    <p><b>&nbsp; [entityClassification]:</b> The requesting Entity's category or classification. There are ONLY 3 possibilities here: </p>\r\n                                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; <b>1 </b>- For Business Names </p>\r\n                                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; <b>2 </b>- For Companies </p>\r\n                                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; <b>3 </b>- For Incorporated Trustees </p>\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n\r\n\r\n                        <div class=\"accordion-item\">\r\n                            <h2 class=\"accordion-header text-light\" id=\"headingTwo\">\r\n                                <button style=\"background-color: #5F8575\" type=\"button\" class=\"accordion-button collapsed text-light\" data-bs-toggle=\"collapse\" data-bs-target=\"#collapseTwo\">2. Basic API call with Company and Director(s) details</button>\r\n                            </h2>\r\n                            <div id=\"collapseTwo\" class=\"accordion-collapse collapse\">\r\n                                <div class=\"card-body\">\r\n                                    <p>Bootstrap is a sleek, intuitive, and powerful front-end framework for faster and easier web development. It is a collection of CSS and HTML conventions. <a href=\"https://www.tutorialrepublic.com/twitter-bootstrap-tutorial/\" target=\"_blank\">Learn more.</a></p>\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n\r\n\r\n                        <div class=\"accordion-item\">\r\n                            <h2 class=\"accordion-header\" id=\"headingThree\">\r\n                                <button style=\"background-color: #5F8575\" type=\"button\" class=\"accordion-button collapsed text-light\" data-bs-toggle=\"collapse\" data-bs-target=\"#collapseThree\">3. Basic API call with Company and Shareholder(s) details</button>                     \r\n                            </h2>\r\n                            <div id=\"collapseThree\" class=\"accordion-collapse collapse\">\r\n                                <div class=\"card-body\">\r\n                                    <p>CSS stands for Cascading Style Sheet. CSS allows you to specify various style properties for a given HTML element such as colors, backgrounds, fonts etc. <a href=\"https://www.tutorialrepublic.com/css-tutorial/\" target=\"_blank\">Learn more.</a></p>\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n\r\n\r\n                        <div class=\"accordion-item\">\r\n                            <h2 class=\"accordion-header\" id=\"headingFour\">\r\n                                <button style=\"background-color: #5F8575\" type=\"button\" class=\"accordion-button collapsed text-light\" data-bs-toggle=\"collapse\" data-bs-target=\"#collapseFour\">4. Basic API call with Company and Secretary details</button>                     \r\n                            </h2>\r\n                            <div id=\"collapseFour\" class=\"accordion-collapse collapse\">\r\n                                <div class=\"card-body\">\r\n                                    <p>CSS stands for Cascading Style Sheet. CSS allows you to specify various style properties for a given HTML element such as colors, backgrounds, fonts etc. <a href=\"https://www.tutorialrepublic.com/css-tutorial/\" target=\"_blank\">Learn more.</a></p>\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n\r\n\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div class=\"col-sm-3\">\r\n                <div class=\"row align-bottom\">\r\n                    <div class=\"col-sm-12\">\r\n                        <div class=\"row\">\r\n                            <div class=\"col-sm-3\">\r\n                                <br />\r\n                                <i class=\"fas fa-user-circle fa-5x justify-content-center\" style=\"color: #DDDDDD\"></i>\r\n                                <br />\r\n                                <br />\r\n                            </div>\r\n                            <div align=\"left\" class=\"col-sm-9\" style=\"border-left: 1px ridge #EEEEEE\">\r\n                                <br />\r\n                                <div class=\"row\">\r\n                                    <div class=\"col-sm-12\">\r\n                                        <h4><b>Organization</b></h4>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"row\">\r\n                                    <div class=\"col-sm-12\">\r\n                                        <h5 style=\"color: #778899\"><b>{{this.$session.get('organization')}}</b></h5>\r\n                                    </div>\r\n                                </div>\r\n                                <br />\r\n                                <div class=\"row\">\r\n                                    <div class=\"col-sm-12 text-secondary\">\r\n                                        <h4><b>Current <i class=\"fas fa-wallet\"></i> Balance</b></h4>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"row\">\r\n                                    <div class=\"col-sm-12\">\r\n                                        <h5 class=\"text-danger\"><b>&#8358; &nbsp; {{ Number(this.units).toLocaleString() }}</b></h5>\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <br />\r\n        <br />\r\n        <br />\r\n        <br />\r\n        <br />\r\n        <br />\r\n        <br />\r\n        <br />\r\n        <br />\r\n        <br />\r\n        <br />\r\n        <br />\r\n        <br />\r\n    </div>\r\n</template>\r\n<script>\r\nimport axios from 'axios';\r\nimport NavBar from './navigations/APIDocumentationNav.vue';\r\nexport default ({\r\n    setup() {\r\n        \r\n    },\r\n    data(){\r\n        return{\r\n            header_1: \"API Documentation\",\r\n            csrf: document.querySelector('meta[name=\"csrf-token\"]').getAttribute('content'),\r\n            units: 0\r\n        }\r\n    },\r\n    components:{\r\n        NavBar\r\n    },\r\n    beforeCreate(){\r\n        const rrr = this.$route.params;\r\n        if (!this.$session.exists()) {\r\n        this.$router.push('/');\r\n        }\r\n        else{\r\n            if(this.$session.get(\"role\") != \"Accessor\"){\r\n                alert(\"You do not have privilege to visit this page.\");\r\n                this.$session.destroy();\r\n                this.$router.push('/');\r\n            }\r\n            else{\r\n                //Get Realtime Units\r\n                var postData = {\r\n                    \"username\": this.$session.get(\"username\"),\r\n                }\r\n                try{\r\n                    axios.post(\"http://127.0.0.1:8000/api/get_realtime_units\", postData) \r\n                    .then(response =>{\r\n                        this.units = response.data[\"units\"];\r\n                        console.log(response);\r\n                    })\r\n                }\r\n                catch(err){    \r\n                        console.log(\"Get realtime Units Error: \" + err);\r\n                }\r\n            }   \r\n        }\r\n    }\r\n})\r\n</script>\r\n<style scoped>\r\n/*.accordion-button.collapsed {\r\n  \r\n}*/\r\n\r\n.accordion-button.collapsed::after {\r\n  background-image: url(\"data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23fff'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e\");\r\n}\r\n\r\n.accordion-button::before {\r\n  background-image: url(\"data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23fff'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e\");\r\n}\r\n\r\n</style>\r\n"],"sourceRoot":""}]);
